import * as React from "react";
import * as Levin from "fast-levenshtein";

interface SimilarityState {
  leftContent: string;
  rightContent: string;
  similarityPercent: number;
  differences: number;
}

export class SimilarityDetector extends React.Component<{}, SimilarityState> {
  constructor() {
    super({});

    this.state = {
      differences: 0,
      leftContent: "",
      rightContent: "",
      similarityPercent: 0
    };

    this.handleLeftUpdate = this.handleLeftUpdate.bind(this);
    this.handleRightUpdate = this.handleRightUpdate.bind(this);
  }

  private handleLeftUpdate(event: React.ChangeEvent) {
    this.setState({ ...this.state, leftContent: event.target.textContent });
  }
  private handleRightUpdate(event: React.ChangeEvent) {
    this.setState({ ...this.state, rightContent: event.target.textContent });
  }

  private HandleLevin() {
    let differences = Levin.get(
      this.state.leftContent,
      this.state.rightContent
    );

    this.setState({ ...this.state, differences });
  }

  render() {
    return (
      <div>
        <form>
          <textarea id={"LeftText"} onChange={this.handleLeftUpdate} />
          <textarea id={"RightText"} onChange={this.handleRightUpdate} />
          <label>Similarity: {this.state.similarityPercent}</label>
          <label>Differences: {this.state.differences}</label>
        </form>
      </div>
    );
  }
}
